@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;500;700&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Poppins', sans-serif;
}

.container {
    max-width: 670px;
    width: 100%;
    margin: 40px auto 0 auto;
    padding: 0 15px;
}
.flex{
    display: flex;
}
.add-task{
    justify-content: space-between;
    align-items: center;
    padding: 20px 0;
    .add-icon{
        margin: 0 0 0 10px;
        img{
            cursor: pointer;
        }
    }
}
input[type="text"]{
    padding: 7px 10px;
}
input[type="text"]:focus{
    outline: 0;
}
.overlay{
    position: fixed;
    top: 0;
    left: 0;
    max-width: 100%;
    width: 100%;
    background: rgba(0,0,0,0.7);
    height: 100%;
    display: none;
    z-index: 1;
}
.todo-popup{
    position: fixed;
    z-index: 22;
    width: 100%;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    display: none;
    .popup-container{
        max-width: 400px;
        box-shadow: 0 0 10px rgba(0,0,0,0.7);
        margin: auto;
        background-color: #fff;
        padding: 20px;
        h2{
            margin: 0 0 10px;
        }
       input{
        width: 100%;
        border: none;
        border-bottom: 1px solid #000;
        margin: 0 0 10px 0;
       }
        textarea{
            padding: 7px 10px;
            height: 150px !important;
            width: 100% !important;
            &:focus{
                outline: 0;
            }
        }
        .popup-close{
            cursor: pointer;
        }
    }
}
.task{
    &-card{
        align-items: center;
        padding: 20px;
        border: 1px solid #ccc;
        border-bottom: 0;
        background: #7d77e0;
        &:last-of-type{
            border-bottom: 1px solid #ccc;
        }
        &.complete{
            opacity: 0.7;
        }
    }
   
    &-complete,&-delete{
        flex-basis: 25%;
        text-align: right;
        img{
            cursor: pointer;
        }
    }
    &-context{
        flex-basis: 60%;
    }
}
.text-right{
    text-align: right;
}
.d-block{
    display: block;
}
.d-none{
    display: none;
}
.task-lists{
    max-height: 400px;
    overflow: auto;
}
/* width */
::-webkit-scrollbar {
    width: 5px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background: #f1f1f1;
  }
  
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #888;
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #555;
  }